.main-container .inner-block {
  text-align: justify;
  display: inline-block;
  width: 600px;
  vertical-align: middle;
  margin-bottom: 70px;
}

.main-container .inner-block p, .main-container .inner-block h2 {
  padding: 20px;
}

.main-container .img-block {
  width: 270px;
  overflow: hidden;
  text-align: center;
}

.main-container .img-block img {
